.header {
    display: flex;
    justify-content: center;

  }
  
  .headerlink-title {
    color: black;
    text-decoration: none;
    padding: 0 5px;
  }

  .logo-img {
    height:40px;
    transition: 0.5s;

  }
  .logo-img:hover{
    transition: 0.5s;
    transform: scale(1.1)
  }

  .custom-nav{
      background-color: rgb(255, 255, 255);
      font-size: 20px;
      margin-left:40px;
      margin-top:5px;
      margin-right:40px;
  }

  .try {
    font-size:0.7em;
    text-decoration: underline;
    color: grey;
    transition: 0.5s;
  }
  .try:hover{
color:black;
transition: 0.5s;
  }

  .code {
    font-size:0.7em;
    transition: 0.5s;
  }
